.main {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: center;
}

.lists {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  text-align: center;
}

.list {
  margin: 10px 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  list-style: none;
  max-height: 250px;
}
